console.log('start');

import Swiper, { Navigation, Pagination, Grid } from 'swiper';
Swiper.use([Navigation, Pagination, Grid]);

import Chart from 'chart.js/auto'

document.addEventListener("DOMContentLoaded", () => {

  const accordionItems = document.querySelectorAll(".accordion__item")

  accordionItems.forEach((el) => {
    el.setAttribute("data-height", el.clientHeight)
    if(!el.classList.contains("accordion__item--active")){
      el.style.height = `${el.querySelector(".accordion__item-title").clientHeight + 15 + 48}px`
    }
    el.addEventListener("click", () => {
      el.closest(".accordion").querySelectorAll(".accordion__item").forEach((item) => {
        item.classList.remove("accordion__item--active")
        item.style.height = `${item.querySelector(".accordion__item-title").clientHeight + 15 + 48}px`
      })
      el.classList.add("accordion__item--active")
      el.style.height = `${el.getAttribute("data-height")}px`
    })
  })

  const swiper = new Swiper(".reviews__swiper", {
     slidesPerView: 2,

      grid: {
        rows: 2,
        fill: "row"
      },
      spaceBetween: 8,
      pagination: {
        el: ".swiper-pagination",
        clickable: true,
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
        breakpoints: {
          0: {
            slidesPerView: 1.1,
            centeredSlides: true,
            loop: true,
            grid: {
              rows: 1,
              fill: "row"
            },
          },
          // when window width is >= 640px
          768: {
            slidesPerView: 2,
            grid: {
              rows: 2,
              fill: "row"
            },
          }
        }
  });

});

// Функция для загрузки JSON файла
        async function loadJSON() {
            try {
                const response = await fetch('../json/График.json');
                if (!response.ok) {
                    throw new Error('Ошибка загрузки файла');
                }
                const data = await response.json();
                return data.Data;
            } catch (error) {
                console.error('Ошибка:', error);
                return null;
            }
        }

        // Функция для создания графиков
        async function createCharts() {
            const data = await loadJSON();
            if (!data) return;

            // Подготовка данных
            const dates = data.map(item => new Date(item.Date).getFullYear());
            const balances = data.map(item => item.Balance);
            console.log(dates)
            // Создание графика баланса
            const balanceCtx = document.getElementById('balanceChart').getContext('2d');

            function filterLabels(labels) {
            const result = [];
            let lastYear = '';

            labels.forEach((label, index) => {
                const date = new Date(label);
                const currentYear = date.getFullYear().toString();

                // Показываем метку только если год изменился или это первая/последняя точка
                if (currentYear !== lastYear || index === 0 || index === labels.length - 1) {
                    result.push(label);
                    lastYear = currentYear;
                } else {
                    result.push(''); // Пустая строка скроет метку
                }
            });

            return result;
         }

            new Chart(balanceCtx, {
                type: 'line',
                data: {
                    labels: dates,
                    datasets: [{
                        label: '',
                        data: balances,
                        borderColor: '#335fe3',
                        borderWidth: 1,
                        tension: 0.1,
                        fill: false,
                        backgroundColor: undefined,
                        pointRadius: 0,
                    }]
                },
                options: {
                  responsive: true,
                   layout: {
                      padding: {
                        top: 20,
                        left: 10,
                        right: 10,
                        bottom: 10
                      }
                  },
                    scales: {
                      x: {
                        grid: {
                          display: true
                        },
                        ticks: {

                        callback: function(value, index, ticks) {
                        return '$' + value;
                    }
                    },
                      },
                      y: {
                        grid: {
                          display: true
                       },
                      }
                    },
                    plugins: {
                       legend: {
                        display: false
                    },
                        tooltip: {
                            callbacks: {
                                label: function(context) {
                                    return 'Баланс: ' + context.parsed.y.toFixed(2);
                                }
                            }
                        }
                    },
                    scales: {
                        x: {
                            ticks: {
                                maxRotation: 45,
                                minRotation: 45
                            }
                        }
                    }
                }
            });

        }

// Запуск создания графиков при загрузке страницы
document.addEventListener('DOMContentLoaded', createCharts);
